*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
  font-size: 13px;
  color: #666;
  background: #fff; }

.container {
  display: flex;
  flex-direction: column;
  height: 400px;
  width: 300px; }

.header {
  display: flex;
  border-bottom: 1px solid #ccc; }
  .header .item {
    flex: 1;
    position: relative;
    height: 30px;
    line-height: 30px;
    text-align: center;
    bottom: -1px;
    cursor: pointer;
    color: #666;
    border-bottom: 1px solid transparent; }
    .header .item.active {
      color: #f45a8d;
      border-bottom: 1px solid #f45a8d; }
    .header .item .num {
      padding: 0 3px;
      border-radius: 3px;
      font-size: 12px;
      margin-left: 5px;
      color: #fff;
      background: red; }

.main {
  flex: 1;
  display: flex; }
  .main .panel {
    flex: 1;
    position: relative;
    display: none;
    padding: 10px; }
    .main .panel.active {
      display: block; }
    .main .panel .item {
      margin-bottom: 10px; }
      .main .panel .item .name {
        font-size: 13px;
        margin-bottom: 5px; }
      .main .panel .item .value {
        display: flex;
        align-items: center; }
        .main .panel .item .value input,
        .main .panel .item .value select {
          height: 25px;
          line-height: 25px;
          padding: 0 5px;
          width: 100%;
          font-size: 12px;
          outline: none;
          color: #444;
          border: 1px solid #ccc; }
      .main .panel .item .save {
        text-align: center;
        height: 30px;
        line-height: 30px;
        cursor: pointer;
        border-radius: 3px;
        letter-spacing: 5px;
        transition: all 0.3s ease;
        color: #fff;
        background: #f45a8d; }
        .main .panel .item .save:hover {
          background: #d62f66; }
      .main .panel .item .tip {
        font-size: 12px;
        margin: 0 0 10px;
        line-height: 1.5;
        color: #888; }
      .main .panel .item.limit_size, .main .panel .item.limit_duration {
        display: none; }
        .main .panel .item.limit_size.active, .main .panel .item.limit_duration.active {
          display: block; }
    .main .panel .disabled {
      display: none;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      font-size: 16px;
      color: #666;
      background: #fff; }
      .main .panel .disabled.show {
        display: flex; }

.footer {
  display: flex;
  justify-content: space-between;
  height: 30px;
  line-height: 30px;
  padding: 0 10px;
  border-top: 1px solid #ccc; }
  .footer a {
    color: #666;
    text-decoration: none;
    transition: all 0.3s ease; }
    .footer a:hover {
      color: #f45a8d; }
